<template>
  <div ref="scroll" class="scroll">
    <div style="overflow: hidden">
      <slot/>
      <div class="loading" v-if="!isFinished">
        <van-loading type="spinner" size="16px" color="#9B9B9D"/>
        <span>加载中...</span>
      </div>
    </div>
    <div v-if="isPulling" class="pull-loading">
      <van-loading type="spinner" size="20px" color="#9B9B9D"/>
    </div>
  </div>
</template>

<script>
import BScroll from 'better-scroll'

export default {
  name: 'scroll',
  props: {
    isFinished: {
      type: Boolean,
      default: true
    },
    clickable: {
      type: Boolean,
      default: false
    },
    probeType: {
      type: Number,
      default: 3
    },
    pullDownRefresh: {
      type: Object,
      default() {
        return null
      }
    }
  },
  data() {
    return {
      isPulling: false
    }
  },
  mounted() {
    this.scroll = new BScroll(this.$refs.scroll, {
      click: this.clickable,
      probeType: this.probeType,
      pullDownRefresh: this.pullDownRefresh ? this.pullDownRefresh : false,
      bounceTime: 350,
      swipeBounceTime: 300,
      swipeTime: 1000
    })

    this.scroll.on('scrollEnd', () => {
      if (this.isFinished) return
      if (this.scroll.y <= (this.scroll.maxScrollY + 50)) {
        this.$emit('scrollToEnd')
      }
    })

    this.scroll.on('pullingDown', () => {
      if (this.isPulling) return
      this.isPulling = true
      this.$emit('pullingDown')
    })
  },
  beforeDestroy () {
    this.scroll.destroy()
  },
  methods: {
    refresh() {
      this.scroll && this.scroll.refresh()
    },
    closeRefresh() {
      this.isPulling = false
      this.scroll.finishPullDown()
    },
    scrollTo(poi) {
      this.scroll.scrollTo(...poi)
    }
  }
}
</script>

<style scoped lang="stylus">
.scroll
  height 100%
  overflow hidden
  position relative
  .loading
    display flex
    align-items center
    justify-content center
    height 80px
    &>span
      margin-left 10px
      font-size 24px
      color #9B9B9D
  .pull-loading
    position absolute
    width 100%
    height 60px
    line-height 60px
    top 20px
    text-align center
    z-index -1
</style>
